//https://www.npmjs.com/package/compare-version?activeTab=code
/**
 * return 1 if ver1 is big, 0 if equal and -1 if ver2 is bigger
 */
export function compareVersion(ver1: string, ver2: string) {
    const ver1Split = ver1.split('.');
    const ver2Split = ver2.split('.');
    const len = Math.max(ver1Split.length, ver2Split.length);

    for (let i = 0; i < len; i++) {
        if ((ver1Split[i] && !ver2Split[i] && parseInt(ver1Split[i]) > 0) || (parseInt(ver1Split[i]) > parseInt(ver2Split[i]))) {
            return 1;
        } else if ((ver2Split[i] && !ver1Split[i] && parseInt(ver2Split[i]) > 0) || (parseInt(ver1Split[i]) < parseInt(ver2Split[i]))) {
            return -1;
        }
    }

    return 0;
};